






































import {Component, Vue} from 'vue-property-decorator';
import {baseUrl} from '../globals';
import axios from 'axios';
import ICafe from '../domain/interfaces/ICafe';
import {Action, State} from 'vuex-class';
import CafeState from '../store/cafe/types';

@Component
export default class CafeList extends Vue {

    @State('cafe')
    public cafeState!: CafeState;

    @Action
    public getCafes: any;

    constructor() {
        super();
        this.getCafes();
    }

    public toCafeInfo(cafeId: number) {
        this.$router.push(`/cafe/${cafeId}`);
    }
}
