import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store/store';
import './bootstrap';
import './registerServiceWorker';
import firebase from 'firebase';
import {config} from './firebaseConfig';
import {addAuthorizationHeaderFromLocalStorage, axiosInterceptorConfig} from '@/domain/util/libraries/AxiosConfig';
import axios from 'axios';
import {isTokenExists} from '@/domain/util/localstorage/token';

Vue.config.productionTip = false;

axiosInterceptorConfig();

// firebase.initializeApp(config);

new Vue({
    router,
    store,
    render: (h) => h(App),
    created() {
        if (localStorage.getItem('vuex')) {
            addAuthorizationHeaderFromLocalStorage(localStorage.getItem('vuex'));
        }
    },

}).$mount('#app');

