





































import {Component, Vue} from 'vue-property-decorator';
import {Action, State} from 'vuex-class';
import BookingState from '../store/booking/types';
import axios from 'axios';
import {baseUrl} from '../globals';
import SuccessNotifier from '../domain/util/notifications/SuccessNotifier';
import moment from 'moment';

@Component
export default class BookingList extends Vue {
    @Action('getBookingList')
    public getBookingList: any;
    @Action('deleteBooking')
    public deleteBooking: any;

    @State('booking')
    public bookingState!: BookingState;

    public getTime(time: string) {
        const result: string = moment(time).format('DD MMM YYYY HH:mm');
        return result;
    }

    public getStatus(statusNumber: number) {
        let status: string;
        if (statusNumber === 1) {
            status = ' ';
        } else if (statusNumber === 2) {
            status = '';
        }

        return status;
    }

    public bookingDelete(id: number) {
        // axios.delete(baseUrl + `booking/${id}/`)
        //     .then(() => {
        //         SuccessNotifier.notify('', '  ');
        //     });
        this.deleteBooking(id);
    }

    public created() {
        this.getBookingList();
    }
}
