












































































import {Component, Provide, Vue, Watch} from 'vue-property-decorator';
import {Action, State} from 'vuex-class';
import CafeState from '../store/cafe/types';
import IWithRoute from '../domain/util/interfaces/IWithRoute';
import axios from 'axios';
import {baseUrl} from '@/globals';
import {getTimezoneOffset} from '@/domain/util/time/timezone';

@Component
export default class CafeInfo extends Vue implements IWithRoute {
    @Action('getCafe')
    public getCafe: any;

    @Action('bookTable')
    public bookTable: any;

    @State('cafe')
    public cafeState!: CafeState;

    get cafeId() {
        return this.cafeState.cafe.id;
    }

    get getTableSize() {
        const index = this.cafeState.cafe.tables.map((e) => {
            return e.id;
        }).indexOf(this.booking.pickedTable);

        return this.cafeState.cafe.tables[index].size;
    }

    public timeOffset: string = getTimezoneOffset();

    @Provide()
    public booking: any = {
        pickedCafe: 0,
        pickedTable: 0,
        pickedTime: '',
        pickedGuests: 0,
        comment: '',
    };

    @Provide()
    public tableSize: number = 0;

    public setTableSize() {
        this.tableSize = this.getTableSize;
    }

    public created() {
        this.getCafe({cafe_id: this.$route.params.cafe_id}).then(() => {
            this.booking.pickedCafe = this.cafeId;
        });
    }

    public book() {
        this.bookTable(this.booking);
    }
}
